import React, { Component } from 'react';
import ProductsContainer from './containers/ProductsContainer';
import MessageContainer from './containers/MessageContainer';
import CartContainer from './containers/CartContainer';
import Home from './components/Home';
import Manage from './components/Manage';

import { BrowserRouter, NavLink, Switch, Route } from "react-router-dom";

class App extends Component {
    render() {
        return (
            
            <BrowserRouter>
                <div className='menu-container'>
                    <ul className="menu">
                        <li className="menu-item ">
                            <NavLink activeClassName="menu-active" to="/Home">Home</NavLink>
                        </li >
                        <li className="menu-item ">
                            <NavLink activeClassName="menu-active" to="/ProductsContainer">Product</NavLink>
                        </li >
                        <li className="menu-item cart-fixed">
                            <NavLink activeClassName="menu-active" to="/MessageContainer"></NavLink>
                            <NavLink activeClassName="menu-active"to="/CartContainer">Cart</NavLink>
                        </li>
                        <li className="menu-item ">
                            <NavLink activeClassName="menu-active"to="/Manage">Manage</NavLink>
                        </li>
                    </ul>
                    
                    <Switch>
                        <Route exact path="/Home">
                            <Home />
                        </Route>
                        <Route exact path="/ProductsContainer">
                            <ProductsContainer />
                        </Route>
                        <Route exact path="/CartContainer">
                            <CartContainer />
                            <MessageContainer />
                        </Route>
                        <Route exact path="/Manage">
                            <Manage />
                        </Route>
                    </Switch>
                </div>
            </BrowserRouter>
            
        );
        
    }
}

export default App;



