import React, { Component } from 'react';

class Home extends Component {
    render() {
        return (
            <header className="home">
                <div className='admin'>
                    <img className="img-store" src='https://cdn2.iconfinder.com/data/icons/apple-tv-1/512/apple_logo-512.png'></img>
                    <h1 className='home__title'>Welcome to NCT phone store</h1>
                </div>
            </header>
           
        );
        
    }
    
}


export default Home;



